				"html": "<h1>Java Array</h1>\n<p>An Array is used to store a collection of data of similar datatype. Arrays always start with the index of 0.</p>\n<p><strong>Syntax:</strong></p>\n<pre><code>dataType[] name_of_array;   // preferred way.\nor\ndataType name_of_array[];  //  works but not preferred way\n</code></pre>\n<h2>Code snippets of above syntax:</h2>\n<pre><code>double[] list; //preferred way.\nor \ndouble list[]; //works but not preferred way.\n</code></pre>\n<p>Note: The style <code>double list[]</code> is not preferred as it comes from the C/C++ language and was adopted in Java to accommodate C/C++ programmers.</p>\n<h2>Creating Arrays:</h2>\n<pre><code>dataType[] name_of_array = new dataType[arraySize];\n</code></pre>\n<h2>Code snippets of the above syntax:</h2>\n<pre><code>double[] List = new double[10];\n</code></pre>\n<h2>Another way to create an Array:</h2>\n<pre><code>dataType[] name_of_array = {value0, value1, ..., valuek};\n</code></pre>\n<h2>Code snippets of above syntax:</h2>\n<pre><code>double[] list = {1, 2, 3, 4};\n</code></pre>\n<p><em>Example of code:</em></p>\n<pre><code>int[] a = new int[] {4,5,6,7,8}; //declare array\nfor (int i=0; i&#x3C;a.length; i++) //loop goes through each index\n{\n    System.out.println(a[i]); //prints the array\n}\n</code></pre>\n<p><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=2\" alt=\":rocket:\" title=\":rocket:\"> <a href=\"https://repl.it/CONn\">Run Code</a></p>\n<p>Output:</p>\n<pre><code>4\n5\n6\n7\n8\n</code></pre>\n<p>Source: <a href=\"https://docs.oracle.com/javase/tutorial/java/nutsandbolts/arrays.html\">Java Arrays</a></p>",
